.App{display:grid;grid-template-columns:1fr 2fr 1fr 1fr;grid-template-rows:auto;grid-template-areas:"header header header header" "main main main sidebar" "footer footer footer footer";width:100%;height:100%}@media screen and (max-width: 550px){.App{grid-template-columns:1fr;grid-template-rows:auto auto 1px;grid-template-areas:"header header header" "main main sidebar" "footer footer footer"}}a,input{outline:none;text-decoration:none;color:#141414}.card{max-width:155px;max-height:350px;text-align:center;display:flex;justify-content:center;align-items:flex-start;margin:10.5px 10.5px 0 10.5px}.card .imgWrapper{max-width:100%;max-height:100%;display:flex;justify-content:center;align-items:center;overflow:hidden;border-radius:10px;box-shadow:5px 5px 10px grey;transform:scale(1);transition:all 100ms ease}.card .imgWrapper:hover,.card .imgWrapper:active{transform:scale(1.2);box-shadow:1px 1px 5px grey}.card .imgWrapper img{width:100%;height:90%}.card h3{padding-top:1.4%}@media screen and (max-width: 550px){.card{width:calc(100% / 3);height:calc(100% / 3)}}.sidebar-wrapper{background-color:#1d1e22;border-left:1px solid black;grid-area:sidebar;z-index:1}.sidebar-wrapper .genre-card,.sidebar-wrapper .sidebar-header{display:flex;justify-content:center;align-items:center;border:1px solid white;color:white}.sidebar-wrapper .genre-card h3,.sidebar-wrapper .sidebar-header h3{letter-spacing:2px}.sidebar-wrapper .sidebar-header{background-color:#6e6447}.sidebar-wrapper .genre-card{background-color:#1d1e22;transition:all 0.1s ease-in}.sidebar-wrapper .genre-card h3{letter-spacing:2px}.sidebar-wrapper .genre-card:hover{background-color:#52586e}@media screen and (max-width: 550px){.sidebar-wrapper{display:none;position:absolute;top:33%;width:100%;padding-top:5%}}@keyframes asideHover{0%{background-color:#2d323c}100%{background-color:#52586e}}
