.App{display:grid;grid-template-columns:1fr 2fr 1fr 1fr;grid-template-rows:auto;grid-template-areas:"header header header header" "main main main sidebar" "footer footer footer footer";width:100%;height:100%}@media screen and (max-width: 550px){.App{grid-template-columns:1fr;grid-template-rows:auto auto 1px;grid-template-areas:"header header header" "main main sidebar" "footer footer footer"}}a,input{outline:none;text-decoration:none;color:#141414}.card{max-width:155px;max-height:350px;text-align:center;display:flex;justify-content:center;align-items:flex-start;margin:10.5px 10.5px 0 10.5px}.card .imgWrapper{max-width:100%;max-height:100%;display:flex;justify-content:center;align-items:center;overflow:hidden;border-radius:10px;box-shadow:5px 5px 10px grey;transform:scale(1);transition:all 100ms ease}.card .imgWrapper:hover,.card .imgWrapper:active{transform:scale(1.2);box-shadow:1px 1px 5px grey}.card .imgWrapper img{width:100%;height:90%}.card h3{padding-top:1.4%}@media screen and (max-width: 550px){.card{width:calc(100% / 3);height:calc(100% / 3)}}.footer-wrapper{background-color:#1d1e22;grid-area:footer;width:100%;height:100%;padding:2% 0;justify-content:center;align-items:center;color:white;display:none}.footer-wrapper span,.footer-wrapper .copy-link{color:white;text-decoration:none;margin-left:5%}.footer-wrapper span a,.footer-wrapper .copy-link a{color:white}@media screen and (max-width: 550px){.footer-wrapper{padding:4% 0;font-size:0.9em;min-height:30px}.footer-wrapper span,.footer-wrapper .copy-link{margin-left:3%}}
