.App{display:grid;grid-template-columns:1fr 2fr 1fr 1fr;grid-template-rows:auto;grid-template-areas:"header header header header" "main main main sidebar" "footer footer footer footer";width:100%;height:100%}@media screen and (max-width: 550px){.App{grid-template-columns:1fr;grid-template-rows:auto auto 1px;grid-template-areas:"header header header" "main main sidebar" "footer footer footer"}}a,input{outline:none;text-decoration:none;color:#141414}.card{max-width:155px;max-height:350px;text-align:center;display:flex;justify-content:center;align-items:flex-start;margin:10.5px 10.5px 0 10.5px}.card .imgWrapper{max-width:100%;max-height:100%;display:flex;justify-content:center;align-items:center;overflow:hidden;border-radius:10px;box-shadow:5px 5px 10px grey;transform:scale(1);transition:all 100ms ease}.card .imgWrapper:hover,.card .imgWrapper:active{transform:scale(1.2);box-shadow:1px 1px 5px grey}.card .imgWrapper img{width:100%;height:90%}.card h3{padding-top:1.4%}@media screen and (max-width: 550px){.card{width:calc(100% / 3);height:calc(100% / 3)}}.header-wrapper{display:flex;align-items:center;justify-content:space-around;flex-direction:row;padding:2% 0 2% 0;margin:0;background-color:#1d1e22;grid-area:header;color:white;z-index:2;user-select:none}@media screen and (max-width: 550px){.header-wrapper{display:none}}.header-wrapper .header-brand{display:flex;justify-content:center;align-items:center;width:50%;height:100%}.header-wrapper .header-brand .logo{width:70px;height:70px;max-block-size:3.6em}.header-wrapper .header-brand span{font-family:vis;font-size:3.5em;letter-spacing:0.4em;margin-left:2%;color:white}@media screen and (max-width: 780px){.header-wrapper .header-brand span{font-size:2em}}.header-wrapper .search{display:flex;justify-content:center;align-items:center;width:50%;height:100%}.header-wrapper .search .searchBar{width:50%;height:70%;border-radius:5%;font-size:1.6em}.header-wrapper .search .searchButton{height:70%;width:15%;display:flex;justify-content:center;align-items:center;font-size:1.4em;margin-left:2%;padding:0.1em;color:white;border-radius:5%;background-size:100%;background-image:linear-gradient(90deg, #f30, #3f19c7);position:relative;z-index:10;user-select:none}.header-wrapper .search .searchButton:before{background-image:linear-gradient(270deg, #f30, #3f19c7);content:"";display:block;position:absolute;top:0;left:0;height:100%;width:100%;opacity:0;border-radius:5%;z-index:-10;transition:opacity 0.4s}.header-wrapper .search .searchButton:hover:before{opacity:1}@media screen and (max-width: 780px){.header-wrapper .search .searchButton{font-size:1em;margin:0}}.header-wrapper .recommend{height:70%;width:20%;display:flex;justify-content:center;align-items:center;border-radius:5%;margin-left:2%;background:linear-gradient(90deg, #3f19c7, #f30);padding:0.1em}.header-wrapper .recommend .recommendButton{color:white;font-size:1.4em}.header-wrapper .recommend:hover{animation:gradiRec 0.1s linear;animation-fill-mode:both}@media screen and (max-width: 900px){.header-wrapper .recommend{display:none}}@keyframes gradiRec{from{background:linear-gradient(90deg, #3f19c7, #f30)}40%{background:linear-gradient(150deg, #3f19c7, #f30)}80%{background:linear-gradient(210deg, #3f19c7, #f30)}to{background:linear-gradient(270deg, #3f19c7, #f30)}}@keyframes gradiSearch{from{background:linear-gradient(90deg, #f30, #3f19c7)}25%{background:linear-gradient(135deg, #f30, #3f19c7)}50%{background:linear-gradient(180deg, #f30, #3f19c7)}75%{background:linear-gradient(225deg, #f30, #3f19c7)}to{background:linear-gradient(270deg, #f30, #3f19c7)}}
