ing:0.25rem 0.5rem;margin:0.25rem;cursor:pointer;background-color:white}.headerMob-wrapper .headerMob .hamburgerButton .logo{color:white}.headerMob-wrapper .headerMob .hamburger-icon{width:3em;height:3em;position:relative;transform:rotate(0deg);transition:0.5s ease-in-out;cursor:pointer}.headerMob-wrapper .headerMob .hamburger-icon span{display:block;position:absolute;height:8px;width:100%;background:white;border-radius:9px;opacity:1;left:0;transform:rotate(0deg);transition:0.25s ease-in-out}.headerMob-wrapper .headerMob .hamburger-icon span:nth-child(1){top:0px}.headerMob-wrapper .headerMob .hamburger-icon span:nth-child(2),.headerMob-wrapper .headerMob .hamburger-icon span:nth-child(3){top:16px}.headerMob-wrapper .headerMob .hamburger-icon span:nth-child(4){top:32px}.headerMob-wrapper .headerMob .open span:nth-child(1),.headerMob-wrapper .headerMob .open span:nth-child(4){top:16px;width:0%;left:50%}.headerMob-wrapper .headerMob .open span:nth-child(2){transform:rotate(45deg)}.headerMob-wrapper .headerMob .open span:nth-child(3){transform:rotate(-45deg)}.headerMob-wrapper .hamburger-links{display:none;position:absolute;top:9%;z-index:2;width:100%;height:24%;padding-top:5%;background-color:#1d1e22}.headerMob-wrapper .hamburger-links .mobSearch{display:flex;justify-content:space-around;align-items:center;flex-direction:column;padding:10%;border-radius:2%;width:80%;max-height:40%}.headerMob-wrapper .hamburger-links .mobSearch .mobSearchBar{height:100%;width:100%;border-radius:2%;font-size:1.4em;padding:5%}.headerMob-wrapper .hamburger-links .mobSearch .mobSearchButton{position:relative;width:50%;margin-top:4%;font-size:1.4em;display:flex;justify-content:center;align-items:center;padding:0.5em;color:white;background:linear-gradient(90deg, #f30, #3f19c7);border-radius:2%}.headerMob-wrapper .hamburger-links .mobSearch .mobSearchButton:hover{animation:gradiSearch 0.1s linear;animation-fill-mode:both}
